.latest-reviews {
  padding: 105px 0 110px;

  &__subtitle {
    margin-bottom: 64px;
  }

  // & .swiper-wrapper-grid {
  //   display: flex;
  // }

  & .container-right {
    margin-bottom: 47px;
  }

}

.card-reviews {
  display: flex;
  flex-direction: column;
  background-color: var(--bl);
  background-repeat: no-repeat;
  background-size: 400px 195px;
  background-position: center top;
  padding-top: 195px;
  border-radius: 10px;

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    color: var(--white);
    padding: 24px 58px 39px 27px;
    letter-spacing: 0px;
  }

  &__time {
    display: block;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    margin-bottom: 8px;
    letter-spacing: 0px;
  }

  &__hashtag {
    display: block;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    margin-bottom: 16px;
  }

  &__text {
    flex: 1 1 auto;
    text-overflow: ellipsis;
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0.18px;
    margin-bottom: 21px;
  }

}

.swiper-slide-latest-reviews {
  flex: 1 0 400px;
}
