.advantages {
  padding: 95px 0 175px;

  &__subtitle {
    margin-bottom: 63px;
  }

  &__inner {
    display: flex;
    align-items: stretch;
  }

  &__bg {
    width: 100%;
    max-width: 821px;
    margin-left: auto;
    height: 100%;
    background-repeat: no-repeat;
    background-size: 821px 574px;
    background-position: center center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 40px;
    max-width: 620px;
    margin: auto;
  }

  &__item {
    width: 290px;
    grid-column: 3 span;
    background-color: var(--orange);
    padding: 32px;
    padding-top: 32px;
    padding-bottom: 29px;
    border-radius: 10px;
  }

  &__head {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    margin-bottom: 17px;
  }

  &__text {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    margin-bottom: 22px;
    letter-spacing: 0px;
  }

  &__more {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    letter-spacing: 0.2px;
    transition: color 0.2s ease-in;

    &:hover {
      color: #e1e1e1;
    }
    
  }

  &__row {
    width: 100%;
    height: 100%;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

}

.row-left {
  width: 100%;
  background-color: #131310;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.row-center {
  width: 97%;
}

.row-right {
  width: 37%;
}
