.catalog-page {
  padding: 64px 0 160px;

  &__models {
    width: 100%;
    padding-left: 40px;
  }

  &__inner {
    display: flex;
  }

  & .wrapper-grid {
    grid-template-columns: repeat(12, 1fr);
  }

  & .card {
    grid-column: 4 span;
    box-shadow: 0px 0px 0px 0px rgba(186, 186, 186, 0.10), 2px 9px 21px 0px rgba(186, 186, 186, 0.10), 6px 38px 38px 0px rgba(186, 186, 186, 0.09), 14px 84px 51px 0px rgba(186, 186, 186, 0.05), 25px 150px 61px 0px rgba(186, 186, 186, 0.01), 39px 235px 67px 0px rgba(186, 186, 186, 0.00);
  }

}

.withdrawal-goods {

  &__select {
    background-image: url('../img/svg-icon/select-arrow.svg');
    background-repeat: no-repeat;
    background-size: 18px;
    background-position: 95% 50%;
    padding: 20px 16px;
    padding-right: 45px;
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    appearance: none;
  }

  &__option {
    display: inline-block;
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    border-radius: 10px;
    margin-bottom: 11px;

    &:last-child {
      margin-bottom: 0;
    }

  }
}

.cards-row {

  &__btn {
  }
}

.cursor-click {
}

.cursor-click--track {
}

.stock-card {
  margin-bottom: auto;
  min-height: 373px;
  padding: 35px 20px;
  background-color: var(--bl);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center right;

  &__info {
  }

  &__promotion {
    display: block;
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.24px;
  }

  &__strong {
    display: block;
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;
  }

  &__what-discount {
    color: var(--orange);
    font-family: var(--font-Bebas);
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 2px;
  }
}
