.form {
  display: flex;
  align-items: stretch;
  margin-bottom: 14px;

  &__label {
    margin-right: 16px;
  }

  &__input {
    width: 346px;
    display: inline-block;
    padding: 14px 17px;
    border-radius: 5px;
    background-color: var(--white);

    &::-webkit-input-placeholder {
      color: var(--light-grey);
      font-family: var(--font-Inter);
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 160%;
    }

  }

  &__btn {
    padding: 14px 38px;
    border-radius: 5px;
  }
}
