.mountain-bikes {
  background-color: var(--bg);
  padding: 72px 0 110px;

  &__subtitle {
    margin-bottom: 67px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 16px;
    margin-right: 20px;
  }

  &__item {
    background-color: var(--dark);
    background-repeat: no-repeat;
    background-position: 202px 26px;
    background-size: 71px 71px;
    width: 302px;
    grid-column: 3 span;
    border-radius: 10px;
    padding: 31px 36px 32px 24px;
  }

  &__header {
    display: flex;
    align-items: center;
    min-height: 64px;
    margin-bottom: 29px;
  }

  &__head {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
  }

  &__item-text {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0px;
  }

  &__info {
    width: 100%;
    max-width: 509px;
  }

  &__bg {
    height: 257px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    margin-bottom: 31px;
  }

  &__text {
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 162%;
    margin-bottom: 39px;
    letter-spacing: 0px;
  }

}
