.tabs {
  display: block;

  &__nav {
    display: block;
  }

  &__nav-item {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__nav-link {
    width: 100%;
    max-width: 320px;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    padding: 16px;
    padding-right: 50px;
    border-radius: 10px;
    background-color: var(--bg);
    text-align: left;
  }

  & .nav__link {
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
  }

  &__nav-btn {
    display: inline-block;
    background-color: transparent;
    background-image: url('../img/svg-icon/arrow-tabs.svg');
    background-repeat: no-repeat;
    background-size: 16px 20px;
    background-position: 95% 50%;
    width: 100%;
    max-width: 320px;
    color: var(--dark);
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;
    padding-right: 25px;
    border-radius: 10px;
    text-align: left;
    cursor: pointer;

    &:focus {
      color: inherit;
      background-color: transparent;
    }

    &:focus-visible {
      background-color: var(--orange);
    }

  }


  &__nav-btn--menu {
    padding: 16px;
    padding-right: 50px;
    background-color: var(--bg);
    background-size: 24px 15px;
  }

  & .tabs__nav-btn--menu:hover {
    background-color: var(--orange);
  }

  & .tabs__nav-btn--menu.tabs__nav-btn--active {
    color: var(--white);
    background-color: var(--orange);
    background-image: url('../img/svg-icon/arrow-tabs-white.svg');
  }


  & .tabs__nav-btn--active  {
    margin-bottom: 24px;
    background-image: url('../img/svg-icon/tabs-rotate.svg');
  }


  &__panel {
    color: var(--bl);

    & .view-link {
      display: inline;
      text-align: left;
    }
  }

  &__panel--active.tabs__panel--color {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 13px;

    & .custom-checkbox {
      display: flex;
      margin-bottom: 0;
    }
  }

  &__panel--menu {
    padding: 0 16px 24px;
  }

}

.tabs-menu {
  margin-bottom: 74px;
}
