.catalog {
  padding: 91px 0px 153px;

  &__subtitle {
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 2.8px;
    margin-bottom: 61px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 16px;
    grid-template-areas:
      "bicycles bicycles bicycles"
      "trade-in spare-parts equipment"
      "trade-in accessories bicycle-stands"
    ;
  }

  &__grid-item {
    padding: 10px;
    background-color: var(--bl);
    background-repeat: no-repeat;
    border-radius: 10px;
  }

  &__grid-item--bicycles {
    background-color: var(--orange);
  }

  &__grid-head {
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;

  }
}

.bicycles {
  grid-area: bicycles;
  width: 100%;
  height: 304px;
  padding: 120px 80px 100px;
  background-position: 393px 4px;
  background-size: 875px 699px;
 }

.trade-in {
  grid-area: trade-in;
  width: 400px;
  padding: 30px;
  padding-top: 74px;
  padding-left: 98px;
  height: 458px;
  background-position: -52px 176px;
  background-size: 503px 503px;
 }

.spare-parts {
  grid-area: spare-parts;
  width: 400px;
  height: 190px;
  padding: 40px;
  padding-top: 63px;
  background-position: 250px 31px;
  background-size: contain;
}

.equipment-item {
  grid-area: equipment;
  height: 239px;
  width: 448px;
  padding: 41px;
  padding-top: 34px;
  background-position: 223px 96px;
  background-size: 225px 235px;
}

.accessories {
  grid-area: accessories;
  height: 251px;
  margin-top: -113px;
  padding: 40px;
  padding-top: 160px;
  background-position: 99px -146px;
  background-size: auto;
}

.bicycle-stands {
  grid-area: bicycle-stands;
  height: 203px;
  margin-top: -65px;
  padding: 39px;
  padding-top: 35px;
  background-position: 276px -25px;
  background-size: 288px 288px;
}
