.contacts {
  padding: 45px 0 130px;

  &__subtitle {
    margin-bottom: 62px;
  }

  &__map {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 500px;
    margin-bottom: 61px;
  }
  &__image {
    margin-top: 100px;
    margin-left: -65px;
  }
}


.contacts-list {
  display: flex;
}

.contacts-item {
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position: 0 0;
  margin-right: 140px;
  padding-left: 48px;

  &:last-child {
    margin-right: 0;
    padding-left: 47px;
  }

}

.contacts-phone {
  display: block;
  color: var(--bl);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  margin-bottom: 9px;
  letter-spacing: 0px;

  &:last-child {
    margin-right: 0;
  }
}

.contacts-address {
  color: var(--bl);
  font-family: var(--font-Inter);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: -0.07px;
}

.contacts-mail {
  display: block;
  color: var(--bl);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 194%;
  letter-spacing: -0.07px;
}

.contacts-works {
  display: block;
  margin-bottom: 10px;
  letter-spacing: -0.5px;
}

.contacts-time {
  color: var(--bl);
  font-family: var(--font-Inter);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: -0.25px;
}
